<template>
  <div class="ui card" style="cursor:pointer">

    <!-- progress bar attached to top of card -->
    <div class="ui olive top attached progress indicating" :data-percent="procents">
      <div class="bar" :style="progressBarStyle"></div>
    </div>

    <div class="content">
      <div style="display:flex; justify-content: space-between; align-items:center;">
        <div>
          <div v-if="!editMode">
            <h4 class="ui sub header">{{ title }}</h4>
            <a class="ui tag label">{{balance}}/{{target}}</a>
          </div>

          <form-crud 
            v-else 
            v-bind="{title, balance, target, editMode: true}"
            @submit="onSubmit" 
            @cancel="editMode = !editMode"
          />
        </div>

        <!-- action buttons -->
        <div style="display:flex;" v-show="!editMode">
          <button @click="editMode = !editMode" class="ui tiny icon button">
            <i class="pencil alternate icon"></i>
          </button>
          <button @click="showMore = !showMore" class="ui tiny icon button">
            <i class="ellipsis horizontal icon"></i>
          </button>
        </div>
      </div>

      <!-- progress bar -->
      <div
        v-show="showMore || editMode"
        class="ui olive progress indicating"
        style="margin: 10px 0px"
        :data-percent="procents">
          <div class="bar" :style="progressBarStyle">
            <div class="progress">{{procents}}%</div>
          </div>
        <!-- <div class="label">70% Funded</div> -->
      </div>
    </div>

  </div>
</template>

<script>
import FormCrud from "@/components/form-crud.vue";

export default {
  components: {
    FormCrud
  },

  props: {
    title: String,
    target: Number,
    balance: Number,
    index: Number,
  },

  data: () => ({
    editMode: false,
    showMore: false
  }),

  computed: {
    progressBarStyle() {
      return { "transition-duration": "300ms", width: this.procents + "%" };
    },

    procents() {
      return ((this.balance * 100) / this.target).toFixed(0);
    },
  },

  methods: {
    onSubmit(item, i) {
      console.log('here')
      this.editMode = !this.editMode
      let { index } = this
      this.$emit('change-item', {...item, index})
    },

  }
};
</script>

