<template>
  <div>

    <navbar 
      v-show="!addingMode" 
      @click-add-btn="toggleAddingMode" 
    />

    <form-crud 
      v-show="addingMode" 
      class="ui segment"
      @submit="addItem" 
      @cancel="toggleAddingMode"
    />

    <!-- divider -->
    <h5 class="ui horizontal divider header">
      <i class="tag icon" style="font-size: 1.1rem;"></i>
      Targets
    </h5>

    <!-- <div class="container">
  <div class="row">
    <div class="col-sm">
      One of three columns
    </div>
    <div class="col-sm">
      One of three columns
    </div>
    <div class="col-sm">
      One of three columns
    </div>
  </div>
</div> -->

    <!-- items -->
    <div class="container">
      <div class="row">
        <div v-for="(item, i) in items" :key="i" class="col-12 col-sm-6 mb-4" >
          <Card 
            v-bind="{ ...item, index: i }"
            :key="i+'c'"
            @change-item="changeItem" 
          />
        </div>

      </div>

    </div>

  </div>
</template>

<script>
// comps
import Navbar from '@/components/navbar.vue'
import Card from '@/components/card.vue'
import FormCrud from '@/components/form-crud.vue'

export default {
  components: {
    Navbar,
    Card,
    FormCrud,
  },

  data: () => ({
    addingMode: false,
    newItem: {
      title: '',
      target: 0,
      balance: 0
    },
    errors: [],
    items: [
      {title: 'Save up money', target: 10000, balance: 250},
      {title: 'Buy a car', target: 900, balance: 650},
    ]
  }),

  methods: {
    toggleAddingMode() {
      this.addingMode = !this.addingMode
    },

    addItem(item) {
      this.items.push(item)
      this.toggleAddingMode()
      this.$notify({
        message: 'Item was created',
        type: 'success'
      });
    },

    changeItem(item) {
      let {index, ...other} = item

      console.log('here2', index)
      console.log('here2', other)

      for (let key of Object.keys(other)) {
        this.items[index][key] = other[key]
      }

       this.$notify({
        message: 'Item was changed',
        type: 'info'
      });
    }
  }
}
</script>

<style lang="scss" src="@/assets/index.scss"></style>
